import React, { Component } from 'react'

import AdornataMidTop from './Images/Adornata/AdornataMidTop.png'
import eight from './Images/Logo/804.png'
import end from './Images/Logo/end.jpg'
import sa from './Images/Logo/sa.png'
import event from './Images/Logo/Event.png'




import Masonry from 'react-masonry-component';

let slide = 
[
{
    title: 'SA Logo',
    image: sa,
  },
  {
    title: 'Adornata',
    image: AdornataMidTop,
  },
  {
    title: '804',
    image: eight,
  },

{
    title: 'Enduin',
    image: end,
  },



{
    title: 'Event',
    image: event,
  },



]

class Home extends Component {
  constructor(props){
    super(props)
    this.state = { }
    
  }
 
  render() {
      const childElements = slide.map(function(attr){
           return (
              <div className='image-mason-container'>
                <div className='image-mason' >
                    <img src={attr.image} />
                </div>
              </div>
            );
        });
        return (
          <div className='work-container'>
             <div className='work-title'>Selected Designs</div>
            <Masonry
                className={'my-gallery-class'} // default ''   
            >
                {childElements}
            </Masonry>
          </div>
        );
  }
}

export default Home
